import * as R from "ramda"

// Function to download and compress a single URL
export async function downloadAndCompressUrl(screen_name, url, zip) {
    try {
        const response = await fetch(url);
        if (!response.ok) {
            throw new Error(`HTTP error: ${response.status}`);
        }
        // console.log(response);
        const blob = await response.blob();
        let filename = url.substring(url.lastIndexOf('/') + 1);
        if (filename.includes('?')) {
          filename = filename.split('?')[0];
        }
        zip.file(`${screen_name}/${filename}`, blob);
        console.log(`Downloaded and compressed: ${url}`);
    } catch (error) {
        console.error(`Failed to download and compress: ${url}`, error);
        // failedUrls.push({ url, retries: 1 });
    }
}

export function parseTimestamp(str) {
  try {
  // const str = "Mon Apr 10 11:30:55 +0000 2023";

  // Split the string into its components
  const parts = str.split(" ");

  // Get the day, year, and time
  const day = parts[2];
  const year = parts[5];
  const time = parts[3];

  // Convert the month from a string to a number (0-11)
  const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];
  const month = monthNames.indexOf(parts[1]);

  // Create a new Date object
  const date = new Date(Date.UTC(year, month, day, ...time.split(":")));

  return date
  } catch (e) {
    return new Date();
  }
}

export function grabBookmarkDate(item) {
  let sortidx = item.sortIndex;
  const shiftedValue = BigInt(sortidx) >> BigInt(20);
  const convertedDate = new Date(Number(shiftedValue));

  return convertedDate;
}

export function mapTweet(tweet) {
  // tx.executeSql(`
  //             INSERT OR IGNORE INTO bookmarks
  //             (entry_id, sort_index, raw_entry, profile_image_url_https, screen_name, name, full_text, legacy_entities, is_expandable, note_tweet_text, note_tweet_entity_set, synced_at, tweeted_at)
  //             VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, ?, ?, datetime('now'), ?);
  //           `,

  // .field('profile_image_url_https')
  // .field('screen_name')
  // .field('name')
  // .field('full_text')
  // .field('tweeted_at')
  // .field('note_tweet_text')
  // .field(`'https://twitter.com/' || screen_name || '/status/' || REPLACE(entry_id, 'tweet-', '')`, 'tweet_url')
  var tweetResultType = R.path('content.itemContent.tweet_results.result.__typename'.split("."), tweet);

  let keys = [
    'bookmark_date',
    'profile_image_url_https', 'screen_name', 'name',
    'full_text','note_tweet_text', 'tweeted_at',
    'extended_media'
  ]
  let values;

  if (tweetResultType === 'Tweet') {
    values = [
        grabBookmarkDate(tweet),
        R.path('content.itemContent.tweet_results.result.core.user_results.result.avatar.image_url'.split("."), tweet),
        R.path('content.itemContent.tweet_results.result.core.user_results.result.core.screen_name'.split("."), tweet),
        R.path('content.itemContent.tweet_results.result.core.user_results.result.core.name'.split("."), tweet),
        R.path('content.itemContent.tweet_results.result.legacy.full_text'.split("."), tweet),
        R.path('content.itemContent.tweet_results.result.note_tweet.note_tweet_results.result.text'.split("."), tweet),
        parseTimestamp(R.path('content.itemContent.tweet_results.result.legacy.created_at'.split("."), tweet)).toISOString(),
        R.path('content.itemContent.tweet_results.result.legacy.extended_entities.media'.split("."), tweet)
    ]
  } else if (tweetResultType === 'TweetWithVisibilityResults') {
    values = [
      grabBookmarkDate(tweet),
      R.path('content.itemContent.tweet_results.result.tweet.core.user_results.result.avatar.image_url'.split("."), tweet),
      R.path('content.itemContent.tweet_results.result.tweet.core.user_results.result.core.screen_name'.split("."), tweet),
      R.path('content.itemContent.tweet_results.result.tweet.core.user_results.result.core.name'.split("."), tweet),
      R.path('content.itemContent.tweet_results.result.tweet.legacy.full_text'.split("."), tweet),
      R.path('content.itemContent.tweet_results.result.note_tweet.note_tweet_results.result.text'.split("."), tweet),
      parseTimestamp(R.path('content.itemContent.tweet_results.result.tweet.legacy.created_at'.split("."), tweet)).toISOString(),
      R.path('content.itemContent.tweet_results.result.tweet.legacy.extended_entities.media'.split("."), tweet)
        // parseTimestamp(_.get(tweet, )).toISOString()
    ];
  } else {
    if (tweetResultType) {
      console.error('Unknown tweet type', tweetResultType);
    }
    return null;
  }

  let output = R.zipObj(keys, values);
  output['tweet_url'] = `https://x.com/${output.screen_name}/status/${tweet.entryId.replace('tweet-', '')}`
  // .field(, 'tweet_url')

  return output
}

export function downloadCSV(jsonData, fileName) {
  // Convert JSON data to CSV format
  const csvData = convertToCSV(jsonData);

  // Create a Blob object from the CSV data
  const blob = new Blob([csvData], { type: 'text/csv' });

  // Create download URL for the Blob object
  const url = URL.createObjectURL(blob);

  // Create an anchor element for the download link
  const anchorElement = document.createElement('a');
  anchorElement.href = url;
  anchorElement.download = fileName;

  // Programmatically trigger the download
  anchorElement.click();

  // Clean up the temporary elements
  URL.revokeObjectURL(url);
  // anchorElement.remove();
}

export function convertToCSV(jsonData) {
  // Extract the headers from the JSON data
  let headers = Object.keys(jsonData[0]);

  let dontInclude = ["extended_media", "org_item", "bookmark_date"];

  headers = R.without(dontInclude, headers);

  // Create an array of CSV rows
  const rows = [];

  // Add headers to the rows array
  rows.push(headers.join(','));

  // Process each object in the JSON data
  jsonData.forEach((data) => {
    const values = headers.map((header) => {
      // Escape double quotes in field values
      const raw = data[header] ? data[header].toString() : "";
      const field = raw.replace(/"/g, '""');
      return `"${field}"`;
    });
    rows.push(values.join(','));
  });

  // Join the rows array with newline characters
  return rows.join('\n');
}

export function downloadJSON(jsonData, fileName) {
  const jsonString = JSON.stringify(jsonData);
  const blob = new Blob([jsonString], { type: 'application/json' });
  const url = URL.createObjectURL(blob);

  const anchorElement = document.createElement('a');
  anchorElement.href = url;
  anchorElement.download = fileName;
  anchorElement.click();

  // document.removeChild(anchorElement);
}

function generateRandomUserId() {
  const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  let userId = '';

  // Generate a 10 character long random user id
  for (let i = 0; i < 20; i++) {
    userId += chars.charAt(Math.floor(Math.random() * chars.length));
  }

  return userId;
}

let _cachedStorage;
export function storage() {
  if (!_cachedStorage)  {
    _cachedStorage = chrome.storage.local;
    // chrome.storage = {};
  }

  return _cachedStorage;
}

var CryptoJS = require("crypto-js/aes");
export function genSecKey() {
  var msg = generateRandomUserId();
  console.log('read key', msg);
  var ciphertext = CryptoJS.encrypt(msg, 'Cgf4zaeuypDZBhD8GqtgRrxqgaZkchx').toString();
  return ciphertext;
}

export async function getSecKey() {
  let cached = await storage().get("hr_state");
  if (cached.hr_state) {
    return cached.hr_state.enc_key;
  } else {
    let newKey = genSecKey();
    await storage().set({ hr_state: { enc_key: newKey } });
    return newKey;
  }
}

export async function getClientId() {
  let cached = await storage().get("client_id");
  console.log('cached', cached);

  if (cached.client_id) {
    return cached.client_id;
  } else {
    let newId = generateRandomUserId();
    await storage().set({ client_id: newId });
    return newId;
  }
}

export async function checkUnlockStatus() {
  let cachedResult = await storage().get("unlock_status");

  if (cachedResult['unlock_status']) {
    console.log('cachedResult', cachedResult)
    return true;
  } else {
    let clientId = await getClientId();
    let res = await fetch(`https://2p36w37sqaym5ksh47m3br6owe0xkebc.lambda-url.ap-southeast-1.on.aws?clientId=${clientId}`);
    let content = await res.json();
    // if it's granted...
    if (content.granted) {
      await storage().set({ "unlock_status": true });
    }
    return content.granted;
  }
}