import { WindowMessenger, connect } from 'penpal/dist/penpal.mjs';
import { getSecKey } from './utils';

console.log('Bridge loaded');

const messenger = new WindowMessenger({
  remoteWindow: window.parent,
  // Defaults to the current origin.
  allowedOrigins: [
    'https://dev.toolboxforweb.xyz:8787',
    'https://bookmarks.toolboxforweb.xyz'
  ],
});

function getData(keys) {
  console.log('getData', keys);
  return chrome.storage.local.get(keys);
}

let connection = connect({
  messenger,
  methods: {
    getData,
    getSecKey,
  },
})
